@import url(https://fonts.googleapis.com/css?family=Quicksand:300,regular,500,600,700);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Quicksand';
}
body {
    background: #f1f1f1;
    overflow-x: hidden;
}
.livepop {
    font-size: 1.7rem;
    text-align: center;
    margin-top: 2rem;
    text-shadow: 0 2px 4px rgba(0,0,0,0.2);
    -webkit-animation: scalesize 2s ease infinite;
            animation: scalesize 2s ease infinite;
}
@-webkit-keyframes scalesize {
    0% {
        text-align: center;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        text-align: center;
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        text-align: center;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@keyframes scalesize {
    0% {
        text-align: center;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        text-align: center;
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        text-align: center;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
.titleCard {
    text-align: center;
    font-size: 2.2rem;
    padding: 10px;
    margin-top: 1rem;
    text-shadow: 0 2px 4px rgba(0,0,0,0.2);
    font-weight: 800;
}

ul .card {
    margin-top: 1rem;
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.card__main {
    background: #fff;
    max-width: 400px;
    width: 90%;
    height: 210px;
    border-radius: 5px;
    padding: 10px;
    box-shadow: 0 10px 40px rgba(0,0,0,0.06);
    margin: 20px;
    overflow: hidden;
}

.card__inner {
    position: relative;
    width: 100%;
    height: 100%;
}
.card__name {
    text-align: center;
    margin-top: 15px;
    font-weight: bold;
    text-shadow: 2px 4px 10px rgba(0,0,0,0.2);
    font-size: 2rem;
}
.card__total {
    font-size: 2.2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 2rem;
    font-weight: 700;
    color: #777;
    text-shadow: 2px 4px 10px rgba(0,0,0,0.1);
    height: 100%;
    margin-top: 2.5rem;
}
